import { AppReducer, AppState } from 'store/constants';
import { OrderErrorPayload } from 'store/constants/native';

const mapOrderErrorTypeToProps: Record<OrderErrorPayload['type'], AppState['errorPage']> = {
    partner: {
        title: ' \n  ',
        subtitle: ',   ',
        retryButtonText: '',
        closeButtonText: ' ',
    },
    payment: {
        title: ' \n  ',
        subtitle: ',   ',
        retryButtonText: '',
        closeButtonText: ' ',
    },
    timing: {
        title: ' \n  8:00  23:00',
        subtitle: ' ,    ',
        closeButtonText: ' ',
    },
};

export const errorPageView: AppReducer = (state, action) => {
    switch (action.type) {
        case 'ORDER_ERROR': {
            state.errorPage = mapOrderErrorTypeToProps[action.payload.type];
            break;
        }
        case 'LAST_ORDER_ERROR': {
            state.errorPage = {
                title: '    ',
                subtitle: ',   ',
                retryButtonText: '',
                closeButtonText: ' ',
            };
            break;
        }
        default: {
            break;
        }
    }
};
